import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Layout from '../views/Layout.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/layout',
    name: 'layout',
    component: Layout,
    children: [
      // 主页
      {
        path: '/home',
        name: 'home',
        component: Home
      },
      // 用户登录
      {
        path: '/login',
        name: 'login',
        component: () =>
          import(/* webpackChunkName: "Login" */ '../views/user/Login.vue')
      },
      // 个人中心
      {
        path: '/personCenter',
        name: 'personCenter',
        component: () =>
          import(
            /* webpackChunkName: "PersonCenter" */ '../views/user/PersonCenter.vue'
          )
      },
      // 搜索
      {
        path: '/search',
        name: 'search',
        component: () =>
          import(
            /* webpackChunkName: "Search" */ '../views/shopping/Search.vue'
          )
      },
      // 商品详情
      {
        path: '/details',
        name: 'details',
        component: () =>
          import(
            /* webpackChunkName: "Search" */ '../views/shopping/Details.vue'
          )
      },
      // 购物车
      {
        path: '/cart',
        name: 'cart',
        component: () =>
          import(/* webpackChunkName: "Cart" */ '../views/shopping/Cart.vue')
      },
      // 支付
      {
        path: '/pay',
        name: 'pay',
        component: () =>
          import(/* webpackChunkName: "Pay" */ '../views/shopping/Pay.vue')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
